<template>
<div>
  <svg id="svg" viewBox="0 0 100 100"
     xmlns="http://www.w3.org/2000/svg" 
     xmlns:xlink="http://www.w3.org/1999/xlink"
     >       
  <image xlink:href="data:image/jpeg;base64," height="100" width="100"/>    
  <g>
  <rect width="28" height="22" style="fill:white;" x='39' y='61' >
    
  </rect>
    <text x="39" y="60" font-color="black" font-size="4">
     <tspan x="39" dy="1.2em" v-for="line in message">{{line}}</tspan>
    </text>
  </g>
</svg>
</div>
</template>
<script>
function chunkSubstr(str, size) {
  var numChunks = Math.ceil(str.length / size),
      chunks = new Array(numChunks);

  for(var i = 0, o = 0; i < numChunks; ++i, o += size) {
    chunks[i] = str.substr(o, size);
  }

  return chunks;
}
export default {
    props :{
        fathername:''
    },
    computed:{
        message (){
            let data = '祖克伯祝「'+this.fathername+'」父親節快樂';
            data = chunkSubstr(data,8)
            return this.fathername.length>0? data : ['祖克伯祝「」','父親節快樂'] 
        }
    }
}
</script>




